<template>
    <div class="card padd">
      <div class="card-t flex">
        <span class="iconfont icon-information"></span>
        <h3>{{categorys.category_title}}</h3>
        <p class="iconfont icon-gengduo"></p>
      </div>         
      <ul class="card-nav flex">
        <li @click="$refs.list.swiper.slideTo(index)" :class="{'b-active' : news_index===index}" v-for="(items,index) in categorys.news_list" :key="index">{{items.news_type}}</li>
        <!-- <li @click="$refs.list.swiper.slideTo(1)" :class="{'b-active' : news_index===1}">新闻</li>
        <li @click="$refs.list.swiper.slideTo(2)" :class="{'b-active' : news_index===2}">公告</li>
        <li @click="$refs.list.swiper.slideTo(3)" :class="{'b-active' : news_index===3}">活动</li>
        <li @click="$refs.list.swiper.slideTo(4)" :class="{'b-active' : news_index===4}">赛事</li> -->
      </ul>
      <div class="card-news">             
        <swiper :options="{
            autoHeight:true,
            onSlideChangeEnd:function(){
                news_index = $refs.list.swiper.realIndex
            }
            }" 
        ref="list" >
          <swiper-slide  v-for="(list,index) in categorys.news_list" :key="index">
            <a href="" class="flex flex-b min-width" v-for="(item,i) in list.type_news_list" :key="i">
              <span>[{{item.news_type}}]</span>
              <span class="flex-1 ellipsis news-title">{{item.news_title}}</span>
              <span>{{item.news_date}}</span>
            </a>        
          </swiper-slide> 
        </swiper>           
      </div>           
    </div> 
</template>
<script>
export default { 
  props:{    
    categorys:{type:Object},
  },  
  data() {
    return {     
        news_index:0, 
        active:0,                   
    }
    
  },  
  methods:{       
        changeIndex(){
          this.news_index=this.$refs.list.swiper.realIndex
        },
                   
  },
  computed:{
      
  },
  mounted() { 
   
  }
}
</script>
<style lang="scss">
.card{
      margin-top:1.5385rem;
      padding-bottom: 1.3846rem;
      width: 100%;
      height: 20.7692rem;      
      background: #fff; 
      .card-t{
        height:3.8462rem;
        line-height: 3.8462rem;
        justify-content:space-between;
        border-bottom:0.0769rem solid #f1f1f1;
        h3{
          flex: 1;
          text-align: left;
          font-size: 1.2308rem;
          color:#333;
          text-indent: 0.7692rem;
        }
        span{
          display: block;
          font-size: 1.1538rem;
        }
        p{
          font-size:1.5385rem;
        }
      }
      .card-nav{
        width: 100%;
        margin: 0.7692rem 0;
        height:1.8462rem;
        line-height: 1.8462rem;
        font-size:1rem;
        color:#333; 
        justify-content: space-between;   
      }
      .card-news{
        // position: relative;
        width: 100%;
        color:#333;
        font-size: 0.9231rem;
        a{
          color:#333;
          font-size:0.9231rem;
          line-height:2rem;
          .news-title{
            padding-left:0.6154rem; 
          }
        }
        
      }

    }
</style>






