import Vue from 'vue'
import Router from 'vue-router'
import Home from '../Home/Home.vue'
import Main from '../Home/Main.vue'
import SCenter from '../Home/SCenter.vue'
import GCenter from '../Home/GCenter.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      component: Home,
      children: [
        {
          path: '/home/main',
          component: Main
        },
        {
          path: '/home/sh_center',
          component: SCenter
        },
        {
          path: '/home/gr_center',
          component: GCenter
        },
        {
          path: '',
          redirect: '/home/main'
        }
      ]
    }
  ]
})



