<template>
  <div class="home-contanier">
    <div class="home-nav">
      <ul class="flex j-content" ref="spanB">
        <li>
          <router-link tag="div" class="home-nav-text" to="/home/main">
            <span class=" p-bottom" :class="getRouter('/home/main')? 'b-bottom':''">首页</span>
          </router-link>
        </li>
        <li>
          <router-link  tag="div" class="home-nav-text" to="/home/gr_center">
            <span class="p-bottom" :class="getRouter('/home/gr_center')? 'b-bottom':''">攻略中心</span>
          </router-link>
        </li>
        <li>
          <router-link tag="div" class="home-nav-text" to="/home/sh_center">
            <span class="p-bottom" :class="getRouter('/home/sh_center')? 'b-bottom':''">赛事中心</span>
          </router-link>
        </li>
      </ul>
    </div>
    <div class="home-content">
      <router-view />
    </div>
  </div>
</template>
<script>
export default {
        methods:{
          getRouter($rt){
            return this.$route.path === $rt;
          }
        }
}
</script>
<style lang="scss">
.home-contanier {
  position: relative;
  .home-nav {
    height: 3.5662rem;
    background: #db9e3f;
    position: relative;
    li {
      width: 100%;
      .home-nav-text {
        display: inline-block;
        width: 100%;
        text-align: center;
        line-height: 3.5662rem;
        span {
          font-size: 1.0769rem;
          opacity: 1;
          color: #fff;
        }
      }
    }
  }  
}
</style>





