<template>  
  <div class="main-content">
    <div class="swiper-img">
      <swiper :options="swiperOption" ref="mySwiper" >
        <!-- slides -->
        <swiper-slide><img src="../../static/images/lbt1.jpeg"></swiper-slide>
        <swiper-slide><img src="../../static/images/lbt2.jpeg"></swiper-slide>
        <swiper-slide><img src="../../static/images/lbt3.jpeg"></swiper-slide>        
        <!-- Optional controls -->
        <div class="swiper-pagination page"  slot="pagination"></div>       
      </swiper>
    </div> 
    <div class="nav-list">
      <div class="main-nav" ref="bscroll" v-show="nav_show">
        <div class="bscroll-container content" ref="content">
              <div class="nav-t">
            <span class="iconfont icon-mingpian"></span>
            <p>报料站</p>
              </div>
          <div class="nav-t">
            <span class="iconfont icon-shu"></span>
            <p>故事站</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-shoutidai"></span>
            <p>周边商城</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-information"></span>
            <p>体验服</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-guide2zhiyin"></span>
            <p>新人专区</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-zhuangshihuahuajuan"></span>
            <p>荣耀传承</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-shequ"></span>
            <p>同人社区</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-27"></span>
            <p>王者营地</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-weixin"></span>
            <p>公众号</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-banben"></span>
            <p>版本介绍</p>
          </div>
        </div>     
      </div>     
      <div class="main-navs flex" ref="nav" v-show="!nav_show">
      <div class="nav-t flex">
        <span class="iconfont icon-mingpian"></span>
        <p>报料站</p>
      </div>
       <div class="nav-t flex">
        <span class="iconfont icon-shu"></span>
        <p>故事站</p>
      </div>
       <div class="nav-t flex">
        <span class="iconfont icon-shoutidai"></span>
        <p>周边商城</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-information"></span>
        <p>体验服</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-guide2zhiyin"></span>
        <p>新人专区</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-zhuangshihuahuajuan"></span>
        <p>荣耀传承</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-shequ"></span>
        <p>同人社区</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-27"></span>
        <p>王者营地</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-weixin"></span>
        <p>公众号</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-banben"></span>
        <p>版本介绍</p>
      </div>
      </div>
      <div class="to-up flex" @click="toUp()" >
        <span class="iconfont icon-shouqi-copy" :class="nav_show ? 'span-active': ''"></span>
        <p>{{nav_show ? '展开':'收起'}}</p>
      </div>
    </div>
   <card-lists :categorys="news_list"></card-lists>   
  </div>
</template>
<script>
import BScroll from "better-scroll";
import newsData from "../data/news.json"
export default {
  data() {
    return {
      swiperOption: {
          pagination: '.swiper-pagination',
          slidesPerView: 'auto',
          centeredSlides: true,
          paginationClickable: true, 
          autoplay : 5000,//可选选项，自动滑动
          loop : true,//可选选项，开启循环         
        }, 
        nav_show:false,
        news_list:{}                      
    }
    
  },  
  methods:{
        toUp(){
          this.nav_show = ! this.nav_show;
          if(this.nav_show){
            this.$nextTick(()=>{
              this._initScroll();
            })
          }               
        },
        _initScroll(){
           let bscrollDom = this.$refs.bscroll;
           this.aBScroll = new BScroll(bscrollDom,{scrollX:true})         
        },
               
  },
  computed:{
     
  },
  mounted() { 
      this.news_list=newsData[0]
  }
}
</script>
<style lang="scss">
  .main-content{
    width: 100%;
    .swiper-img {
      // height: 14rem;
      overflow: hidden; 
      // position: relative; 
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
      .page{
        text-align: right; 
        padding-right:1.5385rem ;  
        .swiper-pagination-bullet{
          background: #fff;
          opacity: 1;
          border-radius: 0.1538rem;
          width: 0.7692rem;
          height: 0.7692rem;
          z-index: 10;      
        }
        .swiper-pagination-bullet-active{
          background: #4b67af;
          z-index: 10;
        }
      }
      
    }
    .nav-list{
      width: 100%;     
      background: #fff; 
      .main-nav{
        width: 100%;        
        margin-top:0.7692rem; 
        overflow: hidden; 
        position: relative; 
        height: 5.3846rem;       
        .bscroll-container{     
          position:absolute; 
          // width: 100%;          
          height: 4.6154rem;
          font-size:1.0769rem;
          color:#333;  
          margin-bottom:0.7692rem;                  
          display: flex;        
          .nav-t{
            width: 8rem;
            // float: left;
            text-align: center;
            margin:0.7692rem 0;
            border-right: 0.0769rem solid #999;                  
            &:last-child{
              border:0;
            }         
            span{                       
              font-size:2.3077rem;
              animation: all 1s                
              }        
            p{
              opacity: 0.6;
            }        
          } 
        }      
      }
      .main-navs{
        width: 100%;             
        flex-wrap: wrap;       
        font-size:1.0769rem;
        color:#333;  
        margin-top:0.7692rem; 
        overflow: hidden;           
        .nav-t{
          text-align: center;
          width: 25%;              
          flex-direction:column;        
          justify-content:center;
          margin:0.7692rem 0;
          border-right: 0.0769rem solid #999;
          &:nth-child(4n){
            border:0;
          }         
          span{                       
              font-size:2.3077rem;
            }        
          p{
            opacity: 0.6;
          }        
        }
      }
      .to-up{
        background:#f9f9f9;
        height: 2.5385rem; 
        text-align: center;
        font-size: 1.0769rem;
        line-height:  2.5385rem;        
        justify-content:center;
      }
    }
    
    
}

</style>





